/**
 * WordPress dependencies
 */
import { TextareaControl } from '@safe-wordpress/components';

/**
 * Internal dependencies
 */
import './style.scss';

import { useAttributes } from '../hooks';

import { DEFAULT_ATTRS } from './config';
import type { FieldSettingProps } from '../types';
import { ClipboardButton } from '@nab/components';

export const ExternalPageScriptSetting = ( {
	name: settingName,
}: FieldSettingProps ): JSX.Element => {
	const [ attributes ] = useAttributes( settingName, DEFAULT_ATTRS );
	const { value, minified } = attributes;

	return (
		<div className="nab-external-page-script">
			<TextareaControl
				value={ value }
				onChange={ () => void null }
				readOnly
			/>
			<ClipboardButton text={ minified } />
		</div>
	);
};
